<script>
export default {
  components:{
  SelectCountry
  },
  data() {
    return {
      x3Maix: [],
      x6Maix: [],
      factory: this.$store.state.factory,
      newsage: this.$store.state.newsage,
      matrixdata: this.$store.state.matrixdata,
    };
  },
  computed:{
    isnewbuy(){
      let that = this;
     
            let newx3arr = [];
            let newx6arr=[];
            for(let i=0;i<this.x3Maix.length;i++){
              newx3arr.push(Number(this.x3Maix[i].split("level")[1]));
            }
            for(let i=0;i<this.x6Maix.length;i++){
              newx6arr.push(Number(this.x6Maix[i].split("level")[1]));
            }
            newx3arr = that.filterX3Arr(newx3arr); //数组过滤
           newx6arr = that.filterX6Arr(newx6arr); //数组过滤
            let totalPrice = that.taximeter(newx3arr, newx6arr);
            return totalPrice > 0;
    }
  },
  created() {
    
  },
  methods: {
    ...mapActions([
      ACTION_TYPES.UPDATE_LANGUAGE,
      ACTION_TYPES.INIT_NEWSAGE,
      ACTION_TYPES.INIT_FACTORY,
      ACTION_TYPES.INIT_NEWSAGE_ROUTER,
      "displayLoding",
      "randomDisplayData",
      "displayTips",
    ]),
    
    upx3x6(){
      let that = this;
      if(that.$store.state.matrixdata.usersActiveX3Levels){
        for(let i=1;i<=12;i++){
          if(that.$store.state.matrixdata.usersActiveX3Levels[`level${i}`]){
            that.x3Maix.push(`level${i}`);
          }else{
            break;
          }
        }
      }
      if(that.$store.state.matrixdata.usersActiveX6Levels){
        for(let i=1;i<=12;i++){
          if(that.$store.state.matrixdata.usersActiveX6Levels[`level${i}`]){
            that.x6Maix.push(`level${i}`);
          }else{
            break;
          }
        }
      }
      
    },
   
    CallSmartMatrixNewsage(obj) {
      const that = this;
      return new Promise(function (resolve, reject) {
        that.$root.callToAccessBlockchain({
          value: obj.value, //转账值
          requestParams: obj.requestParams, //参数
          contractIndexToUse: obj.contractIndexToUse, //合约名称
          methodName: obj.methodName, //方法名称
          managerIndex: "NewsageManager",
          pairtType: obj.pairtType,
          callback: (isSuccess) => {
            isSuccess != undefined ? resolve(isSuccess) : reject();
          },
        });
      });
    },

    clickx3Item(index, data) {
      let tempselectitem = Number(data.split("level")[1]);
      if (tempselectitem == 1) {
        this.x3Maix = ["level1"];
      } else {
        this.x3Maix = ["level1"];
        for (let i = 2; i <= tempselectitem; i++) {
          this.x3Maix.push(`level${i}`);
        }
      }
    },
    clickx6Item(index, data) {
      let tempselectitem = Number(data.split("level")[1]);
      if (tempselectitem == 1) {
        this.x6Maix = ["level1"];
      } else {
        this.x6Maix = ["level1"];
        for (let i = 2; i <= tempselectitem; i++) {
          this.x6Maix.push(`level${i}`);
        }
      }
    },
    filterX3Arr(arr) {
      let that = this;
      let newarr = Array.from(new Set(arr));
      let filterarr = newarr.filter(function (x) {
        return !that.$store.state.matrixdata.usersActiveX3Levels[`level${x}`];
      });
      let sortarr = filterarr.sort((a, b) => {
        return a - b;
      });
      return sortarr;
    },
    filterX6Arr(arr) {
      let that = this;
      let newarr = Array.from(new Set(arr));
      let filterarr = newarr.filter(function (x) {
        return !that.$store.state.matrixdata.usersActiveX6Levels[`level${x}`];
      });
      let sortarr = filterarr.sort((a, b) => {
        return a - b;
      });
      return sortarr;
    },
    taximeter(X3levels, X6levels) {
      let that = this;
      let x3Price = 0;
      if (X3levels.length > 0) {
        x3Price = X3levels.reduce(function (x, y) {
          if (y >= 1 && y <= 12) {
            return x + that.newsage.levelPrice[`level${y}`];
          } else {
            return x;
          }
        }, 0);
      }
      let x6Price = 0;
      if (X6levels.length > 0) {
        x6Price = X6levels.reduce(function (x, y) {
          if (y >= 1 && y <= 12) {
            return x + that.newsage.levelPrice[`level${y}`];
          } else {
            return x;
          }
        }, 0);
      }
      return x3Price + x6Price;
    },
    
    //购买
    [NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL](
      totalPrice,
      X3levels,
      X6levels
    ) {
      const that = this;
      let temObj = {
        contractIndexToUse: "SmartMatrixNewsage", //合约名称
        value: that.$store.state.web3
          .instance()
          .utils.toWei(`${totalPrice}`, "ether"),
        requestParams: { X3levels, X6levels },
        methodName: NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL,
      };
      return that.CallSmartMatrixNewsage(temObj);
    },
    clickbuye(){
      let that = this;
     
            let newx3arr = [];
            let newx6arr=[];
            for(let i=0;i<this.x3Maix.length;i++){
              newx3arr.push(Number(this.x3Maix[i].split("level")[1]));
            }
            for(let i=0;i<this.x6Maix.length;i++){
              newx6arr.push(Number(this.x6Maix[i].split("level")[1]));
            }

            that.BUY(newx3arr,newx6arr,(res)=>{
              if(res&&res.tx){
                that.displayTips({isTips:true,tipsMs:'购买成功'});
                setTimeout(() => {
                          that.displayTips({isTips:false,tipsMs:''});
                        }, 2500);
                
              }else{
                that.displayTips({isTips:true,tipsMs:'购买失败'});
                setTimeout(() => {
                  that.displayTips({isTips:false,tipsMs:''});
                }, 2500);
              }
            });
          
    },
    BUY(X3levels, X6levels, cb) {
      let that = this;
      if(this.$store.state.matrixdata.users.address!=this.$store.state.user.coinbase){
        that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'请使用自己的账号登陆控制台':'Please use your own account to log in to the console'});
              setTimeout(() => {
                that.displayTips({isTips:false,tipsMs:''});
                that.$router.push(`/Signin`);
              }, 2500);
        return;
      }
      X3levels = that.filterX3Arr(X3levels); //数组过滤
      X6levels = that.filterX6Arr(X6levels); //数组过滤
      let totalPrice = that.taximeter(X3levels, X6levels);

      console.log(totalPrice);
      if (totalPrice > 0) {
        that[NEWSAGE_TYPES.EVENT_NEWSAGE_BUY_NEW_LEVEL](
          totalPrice,
          X3levels,
          X6levels
        ).then((res) => {
          if (cb) {
            cb(res);
          }
        });
      } else {
        that.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'请选择要购买的矩阵':'Please select the matrix to buy'});
              setTimeout(() => {
                that.displayTips({isTips:false,tipsMs:''});
              }, 2500);
        }
    },
  },
};
import { mapActions } from "vuex";
import {
  ACTION_TYPES,
  NEWSAGE_TYPES,
} from "@/util/constants";

import SelectCountry from "@/components/SelectCountry.vue";
</script>

