<template>
  <div class="wallet">
    <div style="height: 24rem;">
    <div class="title">{{CONSOLE_WALLET[$store.state.language].TITLE}}</div>
    <div class="address">{{$store.state.matrixdata.users.address}}</div>
     <div class="btn" @click="copyShaneUrl"><span>{{CONSOLE_WALLET[$store.state.language].BUTTON}}</span></div>
    </div>
    

  </div>
</template>

<script>
export default {
  data(){
    return{
      CONSOLE_WALLET
    }
  },
  methods:{
    ...mapActions([
      "displayTips",
    ]),
    copyShaneUrl(){
      var input = document.createElement("input");   // 直接构建input
      input.value = `http://localhost:8080/Signup?leader=${this.$store.state.matrixdata.users.id}`;  // 设置内容
      document.body.appendChild(input);    // 添加临时实例
      input.select();   // 选择实例内容
      document.execCommand("Copy");   // 执行复制
      document.body.removeChild(input); // 删除临时实例
      
                this.displayTips({isTips:true,tipsMs:this.$store.state.language=='CN'?'复制成功':'Copy successfully'});
                setTimeout(() => {
                          this.displayTips({isTips:false,tipsMs:''});
                        }, 2500);
    }
  }
}
import { mapActions } from "vuex";
import {
 CONSOLE_WALLET
} from "@/util/constants";

</script>

